import styles from './styles.scss';

/** Метод для получения зачеркнутого и/или подчеркнутого оформления */
export const useTextDecoration = (lineThrough = false, underline = false): string | undefined => {
  if (lineThrough && underline) {
    return styles.line_through_and_underline;
  }

  if (lineThrough) {
    return styles.line_through;
  }

  return underline ? styles.underline : undefined;
};

export default useTextDecoration;
